.disable-scroll {

	height   : 100vh;
	left     : 0;
	overflow : hidden;
	position : fixed;
	top      : 0;
	width    : 100%;
}


.graph-modal {
	align-items      : center;
	background-color : $black;
	bottom           : 0;
	cursor           : none;
	display          : flex;
	justify-content  : center;
	left             : 0;
	opacity          : 0;
	position         : fixed;
	right            : 0;
	text-align       : center;
	top              : 0;
	transition       : opacity .3s ease, visibility .3s ease;
	visibility       : hidden;
	width            : 100%;
	z-index          : 1000;
}

.graph-modal:before {
	content        : '';
	display        : inline-block;
	height         : 100%;
	vertical-align : middle;
}

.graph-modal__container {
	background-color : transparent;
	border-radius    : 12px;
	cursor           : none;
	display          : none;
	margin           : 50px auto;
	//padding          : 48px 14px 30px 14px;
	max-width        : 1920px;
	position         : relative;
	text-align       : left;
	vertical-align   : middle;
	@media screen and (min-width : $break-table) {
		//padding : 48px 48px 82px 48px;
	}

	@media screen and (min-width : $break-desktop) {
	}
}


.graph-modal__content {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}


.graph-modal__close {
	align-items      : center;
	background-color : transparent;
	border           : none;
	color            : rgba(255, 255, 255, 0.6);
	display          : flex;
	font-size        : 14px;
	font-weight      : 400;
	line-height      : 20px;
	margin-bottom    : 40px;
	margin-left      : 60px;
	padding          : 0;

	span {
		margin-left : 8px;
	}
}

.graph-modal.is-open {
	opacity    : 1;
	transition : opacity .2s ease, visibility .2s ease;
	visibility : visible;
}

.graph-modal__container.graph-modal-open {
	display : inline-block;
}

.fade {
	opacity : 0;
}

.fade,
.fade.animate-open {
	transition : opacity .2s;
}

.fade.animate-open {
	opacity : 1;
}

.fadeInUp {
	opacity   : 0;
	transform : translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
	transition : transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
	opacity   : 1;
	transform : translateY(0);
}
