  margin: 0 !important;
  transform: translate(-50%, -50%) !important;
  width: 7px !important;
}

#fp-nav ul li a.active span, .fp-slidesNav ul li a.active span {
  background-color: rgb(255, 255, 255) !important;
  height: 8px !important;
  width: 8px !important;
}

.footer {
  bottom: 24px;
  display: none;
  left: 24px;
  z-index: 1111;
}
@media screen and (min-width: 764px) {
  .footer {
    bottom: 40px;
    display: block;
    left: 40px;
    position: fixed;
  }
}
.socials__text {
  color: #FFF;
  font-family: "Montserrat";
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  margin-bottom: 16px;
  opacity: 0.5;
}

.socials__item:not(:first-child) {
  margin-left: 32px;
}
.socials__item:hover svg {
  opacity: 1;
  transition: opacity 0.3s ease;
}
.socials__item svg {
  opacity: 0.5;
  transition: opacity 0.3s ease;
}

.cursor {
  background-color: rgb(255, 255, 255);
  border-radius: 100%;
  display: none;
  height: 10px;
  pointer-events: none;
  position: fixed;
  transform: scale(1);
  transition: 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) scale;
  user-select: none;
  width: 10px;
  z-index: 1111;
}
.cursor.active {
  transform: scale(0);
}
.cursor.hover {
  transform: scale(0);
}
@media screen and (min-width: 1340px) {
  .cursor {
    display: block;
  }
}

.follower {
  border: 1px solid rgb(255, 255, 255);
  border-radius: 100%;
  display: none;
  height: 30px;
  pointer-events: none;
  position: fixed;
  transform: translate(2px, 2px);
  transition: 0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.2s cubic-bezier(0.75, -0.27, 0.3, 1.33) background-color;
  user-select: none;
  width: 30px;
  z-index: 1111;
}
.follower.active {
  background-color: rgb(255, 255, 255);
  mix-blend-mode: difference;
  transform: scale(4);
}
.follower.hover {
  background-color: rgb(255, 255, 255);
  mix-blend-mode: difference;
  transform: scale(3);
}
.follower.hover--small {
  background-color: rgb(255, 255, 255);
  mix-blend-mode: difference;
}
@media screen and (min-width: 1340px) {
  .follower {
    display: block;
  }
}

.disable-scroll {
  height: 100vh;
  left: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;
}

.graph-modal {
  align-items: center;
  background-color: #000;
  bottom: 0;
  cursor: none;
  display: flex;
  justify-content: center;
  left: 0;
  opacity: 0;
  position: fixed;
  right: 0;
  text-align: center;
  top: 0;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  visibility: hidden;
  width: 100%;
  z-index: 1000;
}

.graph-modal:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.graph-modal__container {
  background-color: transparent;
  border-radius: 12px;
  cursor: none;
  display: none;
  margin: 50px auto;
  max-width: 1920px;
  position: relative;
  text-align: left;
  vertical-align: middle;
}
.graph-modal__close {
  align-items: center;
  background-color: transparent;
  border: none;
  color: rgba(255, 255, 255, 0.6);
  display: flex;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin-bottom: 40px;
  margin-left: 60px;
  padding: 0;
}
.graph-modal__close span {
  margin-left: 8px;
}

.graph-modal.is-open {
  opacity: 1;
  transition: opacity 0.2s ease, visibility 0.2s ease;
  visibility: visible;
}

.graph-modal__container.graph-modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
}

.fade,
.fade.animate-open {
  transition: opacity 0.2s;
}

.fade.animate-open {
  opacity: 1;
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
}