.our-works {
	padding-top : 94px;
	position    : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.our-works__image {
	bottom   : 0;
	display  : none;
	left     : 0;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		display : block;
		height  : 357px;
		width   : 461px;
	}

	@media screen and (min-width : $break-desktop) {
		height : auto;
		width  : auto;
	}
}

.our-works__container {

	padding : 0 24px;

	@media screen and (min-width : $break-table) {
		margin    : 0 auto;
		max-width : 767px;
		padding   : 0 40px;
	}
	@media screen and (min-width : $break-desktop) {
		margin    : 0 auto;
		max-width : none;
	}
}

.our-works__title {
	margin-bottom : 18px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 64px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
		margin-left   : 73px;
	}

	.title__bottom {
		margin-left : 75px;
		@media screen and (min-width : $break-table) {
			margin-left : 185px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-left : 315px;
		}
	}
}

.our-works__wrap {
	display        : flex;
	flex-direction : column;
	overflow       : hidden;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
		.swiper--works {
			display : none;
		}

		.swiper-nav {
			display : none;
		}
	}
	@media screen and (min-width : $break-desktop) {
		align-items     : end;
		justify-content : flex-end;
	}

	.b-btn--circle {
		display : none;

		@media screen and (min-width : $break-table) {
			display     : flex;
			flex-shrink : 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}


}

.our-works__text {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		max-width : 276px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 76px;
		max-width    : 236px;
	}
}

.our-works__slider {

	@media screen and (min-width : $break-table) {
		display : none;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

//works
.works {

	position : relative;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 0 40px 0;
	}

	.swiper-nav {
		@media screen and (min-width : $break-table) {
			bottom     : 40px;
			margin-top : 0;
			position   : absolute;
			right      : 40px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.works__container {
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : space-between;

	position        : relative;

	@media screen and (min-width : $break-table) {
		justify-content : center;
		padding         : 200px 0 200px 0px;
	}

	@media screen and (min-width : $break-desktop) {
		justify-content : flex-end;
		padding         : 0;
	}

	.swiper-nav {
		bottom   : 0;
		position : absolute;
		right    : 40px;
	}
}

.works__title {
	margin-top : -60px;
	position   : relative;
	z-index    : 5;


	@media screen and (min-width : $break-table) {
		padding-left : 40px;
		.title__bottom {
			margin-left : 100px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		max-width    : 1175px;
		padding-left : 60px;

		.title__bottom {
			margin-left : 315px;
		}
	}
}


.swiper--works {
	margin : 0 -5px;

	@media screen and (min-width : $break-table) {
		margin-left  : -100px;
		min-height   : 550px;
		padding-left : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin       : 0;
		min-height   : 460px;
		padding-left : 150px;
	}

	.swiper-wrapper {
		height : 100%;
		width  : 100%;
	}

	.swiper-slide {
		transition : width .6s ease;
		width      : 50%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}

		picture {
			display : block;
			margin  : 0 5px;
			@media screen and (min-width : $break-table) {
				margin : 0 16px;
			}
		}

		img {
			height     : 100%;
			max-width  : none;
			object-fit : cover;
			width      : 100%;
		}
	}

	.swiper-slide.swiper-slide-prev {

		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}
	}

	.swiper-slide.swiper-slide-next {

		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}
	}

	.swiper-slide.swiper-slide-active {

		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 40%;
		}
	}
}
