.cursor {
	background-color : $white;
	border-radius    : 100%;
	display          : none;
	height           : 10px;
	pointer-events   : none;
	position         : fixed;
	transform        : scale(1);
	transition       : 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.3s cubic-bezier(0.75, -1.27, 0.3, 2.33) scale;
	user-select      : none;
	width            : 10px;
	z-index          : 1111;

	&.active {
		transform : scale(0);
	}

	&.hover {
		transform : scale(0);
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.follower {
	border         : 1px solid $white;
	border-radius  : 100%;
	display        : none;
	height         : 30px;
	pointer-events : none;
	position       : fixed;
	transform      : translate(2px, 2px);
	transition     : 0.6s cubic-bezier(0.75, -1.27, 0.3, 2.33) transform, 0.2s cubic-bezier(0.75, -0.27, 0.3, 1.33) background-color;
	user-select    : none;
	width          : 30px;
	z-index        : 1111;

	&.active {
		background-color : $white;
		mix-blend-mode   : difference;
		transform        : scale(4);
	}

	&.hover {
		background-color : $white;
		mix-blend-mode   : difference;
		transform        : scale(3);
	}

	&.hover--small {
		background-color : $white;
		mix-blend-mode   : difference;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}
