// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight         : 400;
$btn-line-height         : 1.25;
$btn-border-width        : 1px;
$btn-padding             : 10px 24px;
$btn-font-size           : 16px;
$btn-border-radius       : 8px;
$btn-transition          : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big         : 14px 30px;
$btn-font-size-big       : 16px;
// Small
$btn-padding-small       : 8px 30px;
$btn-font-size-small     : 13px;

// COLOR


// White
$btn-white-bg            : #D8D8D8;
$btn-white-color         : #000;
$btn-white-color-outline : none;
$btn-white-color-hover   : #FFF;

// Black
$btn-black-bg            : #3F4148;
$btn-black-color         : #FFF;
$btn-black-color-outline : #121214;
$btn-black-bg-hover      : #121214;
$btn-black-color-hover   : #FFF;


// SPACING
$btn-spacing-x           : 5px;
$btn-spacing-y           : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	cursor             : none;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Red
	&--white {
		background-color : $btn-white-bg;
		color            : $btn-white-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background : rgba(216, 216, 216, 0.2);
			color      : $white;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-white-bg;
				color            : $btn-white-color;
			}
		}
	}

	// Black
	&--black {
		background-color : $btn-black-bg;
		color            : $btn-black-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn--circle {
	align-items      : center;
	animation        : rotating 6s linear infinite;
	background-color : rgba(255, 255, 255, 0.15);
	border-radius    : 100%;
	display          : flex;
	height           : 160px;
	justify-content  : center;
	overflow         : hidden;
	transition       : background-color .3s ease;
	width            : 160px;

	@media screen and (min-width : $break-table) {
		height : 288px;
		width  : 288px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&:hover {
		background-color : $white;
		transition       : background-color .3s ease;
	}

	span {
		align-items      : center;
		background-color : $white;
		border-radius    : 100%;
		color            : $black;
		display          : flex;
		font-family      : 'Montserrat';
		font-size        : 14px;
		font-style       : normal;
		font-weight      : 400;
		height           : 96px;
		justify-content  : center;
		line-height      : 20px;
		overflow         : hidden;
		width            : 96px;


		@media screen and (min-width : $break-table) {
			height : 160px;
			width  : 160px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes rotating {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}

