.contacts {
	padding : 100px 0 20px 0;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.container {
	.socials {

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.contacts__container {
	display               : grid;
	grid-template-columns : repeat(3, 1fr);
	margin-bottom         : 40px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(6, 1fr);
		margin-bottom         : 0;
	}
	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}

}


.contacts__form {
	grid-column : 1/4;


	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
		grid-row    : 1/4;
	}

	.contacts__text {
		color         : rgba(255, 255, 255, 0.5);
		font-size     : 14px;
		font-weight   : 400;
		line-height   : 20px;
		margin-bottom : 24px;
		max-width     : 313px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}
	}
}

.contacts-list__item {
	max-width : 288px;

	&:not(:last-child) {
		margin-bottom : 32px;
	}

	span {
		display : flex;
	}
}

.contacts-list__name {
	color       : $white;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	opacity     : 0.5;
}

.contacts-list__about {
	color          : $white;
	font-size      : 14px;
	font-weight    : 400;
	line-height    : 20px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.tabs-item {
	display : none;

	&.active {
		display : block;
	}
}

.contacts__wrapper {
	display               : grid;
	grid-row-gap          : 40px;
	grid-template-columns : repeat(3, 1fr);


	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		grid-row-gap          : 24px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.tabs-buttons {
	display     : flex;
	grid-column : 1/4;
	grid-row    : 1/1;
	position    : relative;
	z-index     : 10;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
		grid-row    : 1/1;
	}
}

.tabs-item {
	grid-column : 1/4;
	grid-row    : 2/3;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 1/13;
		grid-row    : 2/3;
	}
}

.tabs-item--first {
	grid-row : 2/2;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		grid-row : 2/2;
	}

	.contacts__title {
		font-weight   : 400;
		grid-column   : 1/5;
		margin-bottom : 30px;

		@media screen and (min-width : $break-table) {
			font-size   : 72px;
			line-height : 72px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 111px;
			margin-top    : -72px;
		}

		@media screen and (min-width : 1700px) {
			font-size   : 122px;
			line-height : 122px;
		}
	}

	.contacts-list {
		grid-column : 1/4;
		grid-row    : 3/3;

		@media screen and (min-width : $break-table) {
			display         : flex;
			grid-column     : 1/8;
			grid-row        : 5/5;
			justify-content : space-between;
			width           : 100%;
		}
		@media screen and (min-width : $break-desktop) {
			display     : block;
			grid-column : 1/4;
			grid-row    : 2/2;
		}
	}

	.contacts__form {
		border-bottom  : 1px solid rgba(255, 255, 255, 0.3);
		margin-bottom  : 32px;
		padding-bottom : 40px;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			border-bottom  : none;
			margin-bottom  : 0;
			padding-bottom : 0;
		}
	}
}

.tabs-item--second {
	grid-row : 2/2;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		grid-row : 2/2;
	}

	.contacts__wrap {
		grid-column   : 1/5;
		margin-bottom : 30px;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			grid-row   : 2/2;
			margin-top : -60px;
		}
	}

	.contacts__title {
		font-size   : 28px;
		line-height : 28px;

		@media screen and (min-width : $break-table) {
			font-size   : 72px;
			line-height : 72px;
		}

		@media screen and (min-width : 1200px) {
			font-size      : 55px;
			letter-spacing : .07em;
			line-height    : 61px;
		}

		@media screen and (min-width : 1760px) {
			font-size   : 90px;
			line-height : 95px;
		}
	}

	.contacts__form {
		grid-column : 1/4;
		position    : relative;

		@media screen and (min-width : $break-table) {
			grid-column : 1/4;
			grid-row    : 3/3;
		}
		@media screen and (min-width : $break-desktop) {
			grid-column : 9/13;
			grid-row    : 2/2;
		}
	}
}

.contacts__subtitle {
	color          : rgba(255, 255, 255, 0.5);
	display        : none;
	font-size      : 14px;
	font-weight    : 400;
	line-height    : 20px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		display : block;
	}
}

.contacts__jaw {
	grid-column   : 1/4;
	margin-bottom : 20px;
	text-align    : center;

	@media screen and (min-width : $break-table) {
		grid-column : 4/8;
		grid-row    : 3/3;
		margin-top  : 0;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 6/8;
		grid-row    : 2/2;
		margin-top  : -69px;
	}
}

.contacts-nav {
	align-items : center;
	bottom      : -4px;
	display     : flex;
	left        : 0;
	position    : absolute;
}

.contacts-nav__wrap {
	align-items : center;
	display     : flex;
	gap         : 8px;
}

.contacts-counter {
	color       : rgba(255, 255, 255, 0.5);
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;
	margin-left : 12px;
}

.btn-step {
	border  : none;
	height  : 48px;
	padding : 0;
	width   : 48px;

	svg {
		pointer-events : none;
	}

	svg {
		circle {
			fill         : rgb(255, 255, 255);
			fill-opacity : 0.1;
			transition   : fill 0.3s ease 0s, fill-opacity 0.3s ease 0s;
		}

		path {
			fill : $white;
		}
	}

	&:hover {
		svg {
			circle {
				fill-opacity : 1;
			}

			path {
				fill : $black;
			}
		}
	}
}

.contacts__checkbox {
	align-items   : center;
	cursor        : none;
	display       : inline-flex;
	margin-bottom : 32px;
	margin-top    : 12px;

	span {
		color       : rgba(255, 255, 255, 0.5);

		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;

		&.active {
			color : $white;
		}
	}

}

.contacts__hand {
	bottom   : 0;
	display  : none;
	left     : 400px;
	position : absolute;
	z-index  : 1;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}


.tabs-btn {
	align-items      : center;
	background-color : transparent;
	border           : 1px solid rgba(255, 255, 255, 0.5);
	border-radius    : 8px;
	color            : rgba(255, 255, 255, 0.5);
	display          : flex;
	font-size        : 13px;
	font-weight      : 400;
	justify-content  : center;
	line-height      : 20px;
	padding          : 10px 9px;
	transition       : background-color .3s ease, border .3s ease;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
		padding     : 10px 24px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	&:first-child {
		border-bottom-right-radius : 0;
		border-top-right-radius    : 0;
	}

	&:last-child {
		border-bottom-left-radius : 0;
		border-top-left-radius    : 0;
	}

	&.active {
		border-color : $white;
		color        : $white;
	}

	&:hover {
		background-color : #2B2B2B;
		border           : 1px solid rgba(255, 255, 255, 1);
	}
}


.jaw {
	max-width : 100%;

	&__item {
		transition : fill .3s ease;

		&.active {
			fill : white;
		}

		&:hover {
			fill : white;
		}
	}
}


.swiper--main {
	height : 100%;
	width  : 100%;
}

.slider-main {
	height : 100%;
	width  : 100%;
}
