// -----------------------------------------------
//  Form config
// -----------------------------------------------
// Error
$form-label-color  : #A7A8B1;
$form-label-size   : 12px;
$form-label-weight : 400;
$form-label-margin : 0 0 10px;
$form-error-color  : $red;
$form-error-size   : 13px;
$form-error-weight : 500;

// Hint
$form-hint-color   : #444;
$form-hint-size    : 12px;
$form-hint-weight  : 400;
$form-hint-style   : italic;
$form-hint-margin  : 5px 0 0;

// Grid
$form-grid         : 7px;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
.form {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	position       : relative;

	.b-btn--black {
		width : 100%;
	}

	.b-btn--white {
		width : 100%;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			width : auto;
		}
	}
}

.form__row {
	display        : flex;
	flex-direction : column;
	position       : relative;
	width          : 100%;


	input[type="text"],
	input[type="email"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="password"],
	select,
	textarea {
		vertical-align : top;
		width          : 100%;
	}


	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 40px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	&--error {
		.form__error {
			height  : auto;
			opacity : 1;
		}

		.form__input {
			border-bottom : 2px solid $red;
		}
	}
}

.form__input {
	border-bottom : 1px solid rgba(217, 217, 217, 0.3);
	color         : $white;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 24px;
	position      : relative;
	z-index       : 1;
}

.form__body {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 36px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.form__error {
	color       : $red;
	font-size   : 14px;
	font-weight : 400;
	height      : 0;
	line-height : 20px;
	opacity     : 0;
}

.form__label {
	span {
		color       : $white;
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;
		opacity     : 0.5;
	}
}

.form__group {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 40px;
	}

	.form__row {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 0;
			}
		}

		&:not(:first-child) {
			@media screen and (min-width : $break-desktop) {
				margin-left : 40px;
			}
		}
	}
}

.form__textarea {
	min-height : 80px;
	resize     : none;
	width      : 100%;
}

.form__text {
	color         : rgba(255, 255, 255, 0.5);
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 18px;
	margin-bottom : 32px;
}

.form__step {
	display       : none;
	margin-bottom : 30px;
	width         : 100%;

	&.active {
		display : block;
	}

	&--one {
		position : relative;

		.b-btn--white {
			bottom   : -32px;
			position : absolute;
			right    : 0;
			z-index  : 1;
		}

		.btn-step {
			height  : auto;
			padding : 10px 16px;
			width   : auto;

			@media screen and (min-width : $break-table) {
				padding : 10px 39px;
			}
			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&--two {
		position : relative;

		.b-btn--white {
			bottom   : -32px;
			padding  : 10px 16px;
			position : absolute;
			right    : 0;
			width    : auto;
			z-index  : 1;

			@media screen and (min-width : $break-table) {
				padding : 10px 39px;
			}
			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.form__wrap {
	margin-bottom : 24px;

	h4 {
		color         : $white;
		font-size     : 20px;
		font-weight   : 600;
		line-height   : 20px;
		margin-bottom : 18px;
	}

	p {
		color       : #FFF;
		font-size   : 14px;
		font-weight : 400;
		line-height : 24px;
	}
}

.checkbox-ios {
	-webkit-appearance : none;
	appearance         : none;
	background         : transparent;
	border             : 1px solid #FFF;
	border-radius      : 20px;
	cursor             : pointer;
	height             : 32px;
	margin             : 0 16px;
	outline            : none;
	position           : relative;
	transition         : .5s;
	width              : 56px;
}

.checkbox-ios:before {
	background    : #FFF;
	border-radius : 20px;
	content       : '';
	display       : block;
	height        : 24px;
	left          : 4px;
	position      : absolute;
	top           : 3px;
	transform     : scale(1.1);
	transition    : .5s;
	width         : 24px;
}

.checkbox-ios:checked:before {
	left : 27px;
}


.form__error {
	color       : $form-error-color;
	font-size   : $form-error-size;
	font-weight : $form-error-weight;
	margin-top  : 3px;
	position    : relative;
	z-index     : 3;
}


.form-password {
	position : relative;

	input[type="text"],
	input[type="password"] {
		padding-right : 50px;
		position      : relative;
		z-index       : 1;
	}
}

.form-password__button {
	background                  : transparent;
	border                      : 0;
	bottom                      : 0;
	-moz-box-sizing             : border-box;
	-webkit-box-sizing          : border-box;
	box-sizing                  : border-box;
	cursor                      : pointer;
	opacity                     : .3;
	outline                     : 0;
	padding                     : 0;
	position                    : absolute;
	right                       : 0;
	-webkit-tap-highlight-color : transparent;
	top                         : 0;
	transition                  : opacity .3s ease;
	vertical-align              : middle;
	width                       : 42px;
	z-index                     : 10;

	i {
		position       : relative;
		vertical-align : middle;
		z-index        : 1;
	}

	&:after {
		background : #000;
		border-top : 1px solid #FFF;
		content    : '';
		display    : block;
		height     : 1px;
		left       : 9px;
		position   : absolute;
		top        : 24px;
		transform  : rotate(-30deg);
		transition : opacity .3s ease;
		width      : 24px;
		z-index    : 10;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		&:hover {
			opacity : 1;
		}
	}

	&.active {
		opacity : 1;

		&:after {
			opacity : 0;
		}
	}
}
