.hero {
	//background-image    : url("../images/hero/panther-hero.png");
	//background-position : top 20px right -2px;
	//background-repeat   : no-repeat;
	//background-size     : 237px 177px;
	padding-top : 80px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		background-position : top 20px right 330px;
	}
}

.hero__container {
	margin    : 0 auto;
	max-width : 1166px;
	padding   : 0 24px;
	position  : relative;

	@media screen and (min-width : $break-table) {
		padding : 0 40px;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}

	.b-btn--circle {

		@media screen and (min-width : $break-table) {
			bottom   : -158px;
			left     : 41px;
			position : absolute;
			z-index  : 3;
		}
		@media screen and (min-width : $break-desktop) {
			bottom : -22px;
			left   : -64px;
		}

	}
}

.hero__title {
	align-items   : center;
	margin-bottom : 16px;
	position      : relative;
	z-index       : 5;
	@media screen and (min-width : $break-table) {
		margin-bottom : 18px;
	}
	@media screen and (min-width : $break-desktop) {

	}

	.title__bottom {
		margin-left : 103px;
		@media screen and (min-width : $break-table) {
			margin-left : 261px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-left : 430px;
		}
	}
}

.hero__about {
	color          : $white;
	display        : block;
	font-family    : 'Montserrat';
	font-size      : 10px;
	font-style     : normal;
	font-weight    : 400;
	letter-spacing : 0.46em;
	line-height    : 14px;
	margin-bottom  : 10px;
	max-width      : 120px;
	opacity        : 0.6;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 20px;
		margin-bottom : 18px;
		max-width     : none;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.hero__wrap {
	position : relative;
	z-index  : 2;
}

.hero__text {
	margin-bottom : 24px;
	margin-left   : auto;


	@media screen and (min-width : $break-table) {
		max-width : 276px;
	}
	@media screen and (min-width : $break-desktop) {
		max-width : 406px;
	}
}

.hero__bg {
	display  : block;
	height   : 177px;
	position : absolute;
	right    : 0;
	top      : -148px;
	width    : 237px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : 418px;
		top    : -378px;
		width  : 560px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.scroll-down {
	bottom   : 19px;
	position : absolute;
	right    : -16px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 40px;
		right  : 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.scroll-down__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	opacity        : 0.5;
	position       : relative;

	span {
		display     : block;
		position    : relative;
		top         : -50px;
		transform   : rotate(270deg);
		white-space : nowrap;
	}

	svg {
		display : block;
	}
}
