.about {
	padding-top : 240px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		padding-top : 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.about__container {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	padding               : 0 24px;
	position              : relative;
	z-index               : 3;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		padding               : 0 40px;
	}
	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 40px;
		padding      : 0 80px;
		z-index      : 1;
	}

}

.about__title {
	grid-column   : 1/4;
	margin-bottom : 16px;
	position      : relative;
	z-index       : 2;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/6;
		margin-bottom : 24px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		padding       : 0 80px;
	}

	.title__bottom {
		margin-left : 175px;

		@media screen and (min-width : $break-table) {
			margin-left : 270px;
		}
		@media screen and (min-width : $break-desktop) {
			margin-left : 456px;
		}
	}
}

.about__text {
	grid-column   : 1/5;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 11/13;
	}
}

.b-btn--circle {
	grid-column : 1/5;

	span {
		white-space : break-spaces;
	}

	@media screen and (min-width : $break-table) {
		span {
			white-space : unset;
		}
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 11/13;
	}
}

.about__img {
	height   : 268px;
	position : absolute;
	right    : 47px;
	top      : 24px;
	width    : 201px;
	z-index  : 2;

	@media screen and (min-width : $break-table) {
		bottom  : 0;
		display : block;
		height  : 686px;
		right   : 40px;
		top     : unset;
		width   : 472px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 0;
		height : 750px;
		left   : 170px;
		width  : 522px;
	}

	&:before {
		background : linear-gradient(180deg, #000 13.92%, rgba(0, 0, 0, 0) 100%);
		bottom     : 0;
		content    : "";
		height     : 97px;
		left       : 0;
		position   : absolute;
		transform  : matrix(1, 0, 0, -1, 0, 0);
		width      : 100%;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	img {
		height    : 100%;
		max-width : none;
		width     : 100%;
	}
}
