.header {
	left     : 0;
	padding  : 20px 20px 0 20px;
	position : absolute;
	top      : 0;
	width    : 100%;
	z-index  : 1001;

	@media screen and (min-width : $break-table) {
		padding : 40px 40px 0 40px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.header__logo {
	height   : 40px;
	position : relative;
	width    : 40px;
	z-index  : 1001;

	@media screen and (min-width : $break-table) {
		height : 64px;
		width  : 64px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.header__container {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.header__panel {
	background-color    : black;
	background-image    : url("../images/icon/menu-mob.png");
	background-position : bottom;
	background-repeat   : no-repeat;
	background-size     : contain;
	display             : flex;
	height              : 100vh;
	justify-content     : end;
	left                : 0;
	opacity             : 0;
	position            : absolute;
	top                 : 0;
	transition          : visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
	visibility          : hidden;
	width               : 100%;
	z-index             : 100;

	@media screen and (min-width : $break-table) {
		background-image    : url("../images/icon/menu.png");
		background-position : -306px 202px;
		background-size     : cover;
	}
	@media screen and (min-width : $break-desktop) {
		background-position : left center;
	}


	&.active {
		opacity    : 1;
		transition : visibility 0.3s ease-in-out, opacity 0.3s ease-in-out;
		visibility : visible;
	}
}


.burger {
	background-color : transparent;
	border           : none;
	color            : $white;
	flex-shrink      : 0;
	font-size        : 0;
	height           : 12px;
	padding          : 0;
	position         : relative;
	width            : 20px;
	z-index          : 1001;

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 4px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

	&--active {
		&::before {
			top        : 50%;
			transform  : rotate(45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		&::after {
			top        : 50%;
			transform  : rotate(-45deg);
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		}

		.burger__line {
			transform  : scale(0);
			transition : transform 0.3s ease-in-out;
		}
	}
}

.header__wrapper {
	display         : flex;
	flex-direction  : column;
	height          : 100%;
	justify-content : space-between;
	padding         : 120px 24px 24px 24px;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		padding : 40px 40px;
		width   : auto;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 40px 40px;
	}

	.contacts-list__item {
		max-width : 180px;

		@media screen and (min-width : $break-table) {
			max-width : 120px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : none;
		}

	}

	.socials {
		bottom   : 40px;
		display  : none;
		left     : 40px;
		position : absolute;

		@media screen and (min-width : $break-table) {
			display : block;
		}
	}
}

.nav {

}

.nav__list {
	counter-reset : count;
	padding-left  : 28px;

	@media screen and (min-width : $break-table) {
		padding-left : 0;
	}
}

.nav__item {
	position : relative;

	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	&:before {
		color             : rgba(255, 255, 255, 0.6);
		content           : "0" counter(count);
		counter-increment : count;
		font-size         : 12px;
		font-weight       : 400;
		left              : -31px;
		line-height       : 16px;
		position          : absolute;
		text-transform    : lowercase;
		top               : -5px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			left        : -40px;
			line-height : 20px;
			top         : 0;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.nav__link {
	color                   : $white;
	font-family             : "Druk Wide";
	font-size               : 24px;
	font-weight             : 400;
	letter-spacing          : .03em;
	line-height             : 24px;
	-webkit-text-fill-color : transparent;
	-webkit-text-stroke     : 1px #FFF;
	text-transform          : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 40px;
		line-height : 40px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 64px;
	}

	&:hover {
		color                   : $white;
		-webkit-text-fill-color : unset;
		-webkit-text-stroke     : unset;
	}
}
