// -----------------------------------------------
//  Form config
// -----------------------------------------------

$input-background          : transparent;
$input-border              : 2px solid $white;
$input-padding             : 0 0 15px 0;
$input-radius              : 0;

$input-font-size           : 20px;
$input-font-weight         : 800;
$input-line-height         : 28px;
$input-color               : $white;
$input-transition          : border-color .3s ease;

// Placeholder
$input-placeholder         : rgba(255, 255, 255, 0.5);

// Focus
$input-border-focus        : $white;

// Error
$input-border-error        : #E33212;

// Disabled
$input-border-disabled     : #BCBCBC;
$input-background-disabled : #F9F9F9;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
input[type="text"],
input[type="email"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="password"],
select,
textarea {
	-webkit-appearance  : none;
	background-color    : $input-background;
	border-bottom-width : $input-border;
	border-left-width   : 0;
	border-radius       : $input-radius;
	border-right-width  : 0;
	border-top-width    : 0;
	box-sizing          : border-box;
	color               : $white;
	cursor              : none !important;
	font-size           : 16px;
	font-weight         : 400;
	height              : auto;
	line-height         : 24px;
	max-width           : 100%;
	outline             : 0;
	padding             : $input-padding;
	transition          : $input-transition;

	@media screen and (min-width : $break-table) {

	}

	@include placeholder() {
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;
	}

	&:focus {
		border-color : $input-border-focus;
	}

	&.error {
		border-color : $input-border-error;
		color        : $red;
	}

	&.disabled,
	&[disabled] {
		background-color : transparent;
		border-color     : rgba(255, 255, 255, 0.3);
		color            : $white;
		cursor           : none;
	}

	&[readonly] {
		background-color : transparent;
		border-color     : rgba(255, 255, 255, 0.3);
		color            : $white;
		cursor           : none;
	}
}

select {
	background-image    : url('../images/select.svg');
	background-position : 100% 50%;
	background-repeat   : no-repeat;
	padding-right       : 35px;
}

input[type="radio"],
input[type="checkbox"] {
	cursor         : none;
	display        : inline-block;
	vertical-align : middle;
}

input.disabled {
	cursor : none;
}

textarea {
	height   : auto;
	overflow : auto;
}

.error-message {
	color      : $red;
	margin-top : 5px;
	order      : 1;
}

.jaw-input[type="text"][disabled] {
	&.active {
		border-bottom : 1px solid $white;
	}
}

.form__error {
	color       : #F00;
	font-size   : 14px;
	font-weight : 400;
	height      : 0;
	line-height : 20px;
	opacity     : 0;
	visibility  : hidden;
}

.form__row--error .form__error {
	height     : auto;
	opacity    : 1;
	visibility : visible;
}

.form__row--error .form__input {
	color : #F00;
}


label.checkbox-style,
.checkbox-style {
	color                       : #A7A8B1;
	cursor                      : none;
	display                     : block;
	font-size                   : 13px;
	font-weight                 : 400;
	line-height                 : 1.3;
	margin                      : 0;
	outline                     : 0;
	overflow                    : hidden;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	user-select                 : none;

	> span {
		color          : #A7A8B1;
		display        : inline-block;
		padding        : 2px 0 2px 28px;
		position       : relative;
		vertical-align : top;

		&:before {
			border        : 1px solid #C1C1C1;
			border-radius : 3px;
			box-sizing    : border-box;
			content       : '';
			height        : 20px;
			left          : 0;
			position      : absolute;
			top           : 0;
			transition    : border-color .3s ease, background-color .3s ease;
			width         : 20px;
			z-index       : 1;
		}

		&:after {
			background    : #000;
			border-radius : 3px;
			content       : '';
			height        : 14px;
			left          : 3px;
			opacity       : 0;
			position      : absolute;
			top           : 3px;
			transition    : opacity .3s ease;
			width         : 14px;
			z-index       : 5;
		}
	}

	input[type="checkbox"] {
		cursor                      : none !important;
		height                      : 1px;
		left                        : -30px;
		outline                     : 0;
		position                    : absolute;
		-webkit-tap-highlight-color : transparent;
		top                         : 0;
		width                       : 1px;

		&:checked {
			~ span {
				&:before {
				}

				&:after {
					opacity : 1;
				}
			}
		}

		&.disabled,
		&[disabled] {
			~ span {
				&:after {
					background : #E3E3E3;
				}
			}
		}
	}
}

