.labs {

	position : relative;

	.swiper-nav {
		bottom     : 120px;
		left       : 24px;
		margin-top : 0;
		position   : absolute;

		@media screen and (min-width : $break-table) {
			bottom : 40px;
			left   : unset;
			right  : 40px;
		}

		@media screen and (min-width : $break-desktop) {
		
		}
	}
}

.labs__container {
	display         : flex;
	flex-direction  : column-reverse;
	height          : 100%;
	justify-content : center;
	padding         : 0 24px;
	position        : relative;

	@media screen and (min-width : $break-table) {
		flex-direction  : column;
		justify-content : center;
		padding         : 200px 0 164px 0;
	}

	@media screen and (min-width : $break-desktop) {
		justify-content : flex-end;
		padding         : 0 0 50px;
	}
}

.labs__swiper {

	@media screen and (min-width : $break-table) {
		overflow : hidden;
	}

	@media screen and (min-width : $break-desktop) {
		overflow : visible;
	}

}


.swiper--labs {
	margin   : 0 -5px;
	overflow : hidden;
	@media screen and (min-width : $break-table) {
		margin-left  : -100px;
		min-height   : 550px;
		padding-left : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin       : 0;
		min-height   : 460px;
		padding-left : 150px;
	}

	.swiper-wrapper {

	}

	.swiper-slide {

		transition : width .6s ease;
		width      : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}

		picture {
			display : block;
			margin  : 0 5px;
			@media screen and (min-width : $break-table) {

				margin : 0 16px;

			}
		}

		img {
			height     : 100%;
			max-width  : none;
			object-fit : cover;
			width      : 100%;
		}
	}

	.swiper-slide.swiper-slide-prev {

		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}
	}

	.swiper-slide.swiper-slide-next {

		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 25%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 20%;
		}
	}

	.swiper-slide.swiper-slide-active {

		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 40%;
		}
	}
}

.labs__title {
	position : relative;
	z-index  : 10;
	@media screen and (min-width : $break-table) {
		padding-left : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items  : flex-start;
		margin-top   : -60px;
		max-width    : 900px;
		padding-left : 60px;
	}

	.title__bottom {
		margin-bottom : 24px;
		margin-left   : 71px;
		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
			margin-left   : 182px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 305px;
		}
	}
}
