@font-face {
	font-family  : 'Druk Wide';
	src          : url('../fonts/DrukWideWeb-Regular.woff2') format('woff2'),
	url('../fonts/DrukWideWeb-Regular.woff') format('woff'),
	url('../fonts/DrukWideWeb-Regular.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}


