.footer {
	bottom  : 24px;
	display : none;
	left    : 24px;
	z-index : 1111;


	@media screen and (min-width : $break-table) {
		bottom   : 40px;
		display  : block;
		left     : 40px;
		position : fixed;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.socials__text {
	color         : #FFF;
	font-family   : 'Montserrat';
	font-size     : 10px;
	font-style    : normal;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 16px;
	opacity       : 0.5;
}

.socials__wrapper {

}

.socials__item {


	&:not(:first-child) {
		margin-left : 32px
	}

	&:hover {
		svg {
			opacity    : 1;
			transition : opacity 0.3s ease;
		}
	}

	svg {

		opacity    : 0.5;
		transition : opacity 0.3s ease;
	}
}

