// Arrow
.swiper-button-prev,
.swiper-button-next {
	cursor                      : pointer;
	display                     : inline-block;
	flex                        : 0 0 48px;
	height                      : 48px;
	outline                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 48px;

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {
		&:hover {
			opacity : .7;
		}
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 4px;

}

.swiper-button-next {
	margin-left : 4px;
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}

.swiper-nav {
	display    : flex;
	gap        : 8px;
	margin-top : 40px;

	&__button {
		cursor : none;


		svg {


			circle {
				fill         : $white;
				fill-opacity : 0.1;
				transition   : fill 0.3s ease, fill-opacity 0.3s ease;
			}

			path {
				fill : #FFF;
			}
		}

		&:hover {

			svg {

				circle {
					fill         : $white;
					fill-opacity : 1;
					transition   : fill 0.3s ease, fill-opacity 0.3s ease;

				}

				path {
					fill : $black;
				}
			}
		}
	}
}


